// components/NavBar.js
'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { useState } from 'react';
import { useAuth } from './AuthProvider';

/**
 * NavBar
 *
 * Reads the current user & claims from useAuth()
 * and shows/hides routes based on claims.role.
 *
 * Do NOT change login/logout flows here â€” we only call any signOut
 * function if it's present on the auth object returned by useAuth.
 */
export default function NavBar() {
  const pathname = usePathname();
  const { user, claims, loading, signOut } = useAuth() || {};
  const [open, setOpen] = useState(false);

  // Role helpers
  const role = claims?.role ?? null;
  const clinicId = claims?.clinicId ?? null;

  function isActive(p) {
    if (!pathname) return false;
    return pathname === p || pathname.startsWith(p + '/');
  }

  // Visibility rules
  const canViewPatients = !!role && (['receptionist', 'admin', 'doctor'].includes(role));
  const canViewConsultation = !!role && (['doctor', 'admin'].includes(role));
  const canViewPharmacy = !!role && (['pharmacist', 'admin'].includes(role));

  // Safely call signOut if provided by AuthProvider; do nothing otherwise.
  const handleSignOut = async () => {
    try {
      if (typeof signOut === 'function') {
        await signOut();
      } else {
        // fallback: redirect to /login (don't attempt to alter existing auth flow)
        window.location.href = '/login';
      }
    } catch (err) {
      console.error('Sign out failed', err);
      // best effort: redirect to login
      window.location.href = '/login';
    }
  };

  return (
    <header className="bg-white border-b">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between h-14 items-center">
          <div className="flex items-center gap-4">
            <Link href="/" className="flex items-center gap-3">
              <div className="w-8 h-8 rounded-md bg-indigo-600 flex items-center justify-center text-white font-semibold">CS</div>
              <div className="hidden sm:block">
                <div className="text-sm font-semibold text-slate-800">Clinic Suite</div>
                <div className="text-xs text-slate-500">Clinic EMR</div>
              </div>
            </Link>

            <nav className="hidden md:flex items-center gap-1">
              <NavLink href="/" label="Home" active={isActive('/')} />
              {canViewPatients && <NavLink href="/patients" label="Patients" active={isActive('/patients')} />}
              {canViewConsultation && <NavLink href="/consultation" label="Consultation" active={isActive('/consultation')} />}
              {canViewPharmacy && <NavLink href="/pharmacy" label="Pharmacy" active={isActive('/pharmacy')} />}
            </nav>
          </div>

          <div className="flex items-center gap-3">
            {/* Clinic badge */}
            {clinicId && (
              <div className="hidden sm:flex items-center text-xs text-slate-600 bg-slate-50 border px-2 py-1 rounded">
                <span className="mr-2">Clinic</span>
                <span className="font-medium">{clinicId}</span>
              </div>
            )}

            {/* Account area */}
            <div className="relative">
              <button
                onClick={() => setOpen(o => !o)}
                className="flex items-center gap-2 px-3 py-1 rounded hover:bg-slate-50"
                aria-expanded={open}
                aria-label="Account menu"
              >
                <span className="text-sm text-slate-700">{user?.email ? user.email.split('@')[0] : (user ? user.uid : 'Guest')}</span>
                <svg className="w-4 h-4 text-slate-400" viewBox="0 0 24 24" fill="none" stroke="currentColor">
                  <path strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" d="M6 9l6 6 6-6" />
                </svg>
              </button>

              {open && (
                <div className="absolute right-0 mt-2 w-48 bg-white border rounded shadow z-50">
                  <div className="py-1">
                    {!user && <Link href="/login" className="block px-4 py-2 text-sm text-slate-700 hover:bg-slate-50">Sign in</Link>}
                    {user && (
                      <>
                        <div className="px-4 py-2 text-xs text-slate-500">Signed in as</div>
                        <div className="px-4 py-2 text-sm font-medium text-slate-800">{user?.email ?? user?.uid ?? 'User'}</div>
                        <div className="border-t my-1" />
                        <Link href="/profile" className="block px-4 py-2 text-sm text-slate-700 hover:bg-slate-50">Profile</Link>
                        <button onClick={handleSignOut} className="w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-slate-50">Sign out</button>
                      </>
                    )}
                  </div>
                </div>
              )}
            </div>

            {/* Mobile menu toggle: show minimal nav on small screens */}
            <div className="md:hidden">
              <MobileMenu
                canViewPatients={canViewPatients}
                canViewConsultation={canViewConsultation}
                canViewPharmacy={canViewPharmacy}
                isActive={isActive}
              />
            </div>
          </div>
        </div>
      </div>
    </header>
  );
}

/* ------------------------
   Helper subcomponents
   ------------------------ */

function NavLink({ href, label, active = false }) {
  return (
    <Link href={href} className={`px-3 py-2 rounded text-sm ${active ? 'bg-indigo-600 text-white' : 'text-slate-700 hover:bg-slate-50'}`}>
      {label}
    </Link>
  );
}

/* Mobile menu (small screens) */
function MobileMenu({ canViewPatients, canViewConsultation, canViewPharmacy, isActive }) {
  const [open, setOpen] = useState(false);

  return (
    <div className="relative">
      <button onClick={() => setOpen(v => !v)} className="p-2 rounded hover:bg-slate-50">
        <svg className="w-5 h-5 text-slate-700" viewBox="0 0 24 24" fill="none" stroke="currentColor">
          <path strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" d="M4 6h16M4 12h16M4 18h16" />
        </svg>
      </button>

      {open && (
        <div className="absolute right-0 mt-2 w-44 bg-white border rounded shadow z-50">
          <div className="py-1">
            <Link href="/" className={`block px-4 py-2 text-sm ${isActive('/') ? 'bg-indigo-50' : 'hover:bg-slate-50'}`}>Home</Link>
            {canViewPatients && <Link href="/patients" className={`block px-4 py-2 text-sm ${isActive('/patients') ? 'bg-indigo-50' : 'hover:bg-slate-50'}`}>Patients</Link>}
            {canViewConsultation && <Link href="/consultation" className={`block px-4 py-2 text-sm ${isActive('/consultation') ? 'bg-indigo-50' : 'hover:bg-slate-50'}`}>Consultation</Link>}
            {canViewPharmacy && <Link href="/pharmacy" className={`block px-4 py-2 text-sm ${isActive('/pharmacy') ? 'bg-indigo-50' : 'hover:bg-slate-50'}`}>Pharmacy</Link>}
            <div className="border-t my-1" />
            <Link href="/profile" className="block px-4 py-2 text-sm hover:bg-slate-50">Profile</Link>
            <Link href="/login" className="block px-4 py-2 text-sm hover:bg-slate-50">Sign in</Link>
          </div>
        </div>
      )}
    </div>
  );
}
